
import { defineComponent, onMounted, ref } from "vue";
import { getAnalysisResult } from "@/api/request";
import AnalyseInterface from "./AnalyseInterface.vue";
import router from "@/router";
export default defineComponent({
  components: { AnalyseInterface },
  emits: ["analyse"],
  setup(_, context) {
    const flag = ref(true);
    const analyseType = ref("");
    const analyticDataList = ref();
    const list = [
      {
        name: "",
        src: "/analyse/.png",
        value: "section",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "sectionCompare",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "sectionFlush",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "regionFlush",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "elevationFlush",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "flushContour",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "volume",
      },
      {
        name: "",
        src: "/analyse/.png",
        value: "slope",
      },
      // {
      //   name: "",
      //   src: "https://pic3.zhimg.com/v2-96d58dc7ea889b98102cbad4c53bb91e_720w.jpg?source=172ae18b",
      //   value: "horizon",
      // },
      // {
      //   name: "",
      //   src: "https://pic3.zhimg.com/v2-96d58dc7ea889b98102cbad4c53bb91e_720w.jpg?source=172ae18b",
      //   value: "boundary",
      // },
    ];

    const cardClick = (value: string) => {
      flag.value = false;
      analyseType.value = value;
    };

    const returnParameter = (val: any) => {
      context.emit("analyse", { type: analyseType.value, value: val });
    };

    onMounted(async () => {
      const data = await getAnalysisResult(
        router.currentRoute.value.params.id as string
      );
      if (data != null && (data as any).code === 0) {
        analyticDataList.value = data.data;
      }
    });

    return {
      flag,
      list,
      cardClick,
      analyseType,
      analyticDataList,
      returnParameter,
    };
  },
});
