
interface Tree {
  label: string;
  children?: Tree[];
}
type DataListType = {
  id: string;
  avatar: string;
  name: string;
};
type TableDataType = {
  id: string;
  name: string;
  size: string;
  time: string;
  location: string;
  visualType: string;
  visualId: string;
  flag: boolean;
};
type SelectFileType = {
  fileId: string;
  dataListId: string;
  fileName: string;
  dataListName: string;
  visualType: string;
  visualId: string;
};
import { defineComponent, nextTick, onMounted, ref } from "vue";
import { Search, ArrowLeft } from "@element-plus/icons-vue";
import { fuzzyQueryDataList, findFiles } from "@/api/request";
import { imgBase64 } from "@/utils/common";
import { classTree } from "@/common-config";
export default defineComponent({
  emits: ["returnData"],
  setup(_, context) {
    const defaultProps = {
      children: "children",
      label: "label",
    };
    const treeData: Tree[] = classTree;

    const tree = ref<HTMLElement>();

    const search = ref("");
    const keyword = ref("");
    const total = ref(0);
    const currentPage = ref(1);
    const dataListShow = ref(true);
    const dataList = ref<DataListType[]>([]);
    const selectDataList = ref<DataListType>({ id: "", name: "", avatar: "" });
    const tableData = ref<TableDataType[]>([]);
    const fileList = ref<SelectFileType[]>([]);
    const skeletonFlag = ref(true);
    const loading = ref(false);
    const selectData = ref("");

    const getAvatar = (avatar: string, name: string) => {
      if (avatar === "") {
        return imgBase64(name);
      } else {
        return `/monitor/visual/getAvatar/${avatar}`;
      }
    };

    const formatData = (list: any[]) => {
      dataList.value = [];
      list.forEach((item) => {
        dataList.value.push({
          id: item.id,
          name: item.name,
          avatar: item.avatar,
        });
      });
    };

    const formatTableData = (list: any[]) => {
      tableData.value = [];
      list.forEach((item) => {
        if (item.visualType != "") {
          tableData.value.push({
            id: item.id,
            name: item.fileName,
            visualType: item.visualType,
            time: item.time,
            location: item.location,
            size: item.size,
            flag: false,
            visualId: item.visualId,
          });
          let flag = false;
          for (let i = 0; i < fileList.value.length; i++) {
            if (item.id === fileList.value[i].fileId) {
              flag = true;
              break;
            }
          }
          tableData.value[tableData.value.length - 1].flag = flag;
        }
      });
    };

    const pageChange = async (val: number) => {
      const jsonData = {
        page: val - 1,
        size: 8,
        titleKeyword: keyword.value,
        property: "update_time",
        flag: false,
        type: selectData.value,
      };
      const data = await fuzzyQueryDataList(jsonData);
      if (data != null && (data as any).code === 0) {
        formatData(data.data.list);
        total.value = data.data.total;
        search.value = keyword.value;
      }
    };

    const cardClick = async (index: number) => {
      const data = await findFiles(dataList.value[index].id);
      if (data != null && (data as any).code === 0) {
        formatTableData(data.data);
        selectDataList.value = dataList.value[index];
        dataListShow.value = false;
      }
    };

    const checkboxChange = (val: TableDataType) => {
      if (val.flag) {
        fileList.value.push({
          fileId: val.id,
          fileName: val.name,
          dataListId: selectDataList.value.id,
          dataListName: selectDataList.value.name,
          visualType: val.visualType,
          visualId: val.visualId,
        });
      } else {
        for (let i = 0; i < fileList.value.length; i++) {
          if (val.id === fileList.value[i].fileId) {
            fileList.value.splice(i, 1);
            return;
          }
        }
      }
    };

    const commit = () => {
      context.emit("returnData", fileList.value);
    };

    const nodeClick = async (param: any) => {
      if (param.children === undefined) {
        if (param.label != selectData.value) {
          selectData.value = param.label;
        } else {
          (tree.value as any).setCurrentKey(null);
          selectData.value = "";
        }
        const jsonData = {
          page: 0,
          size: 8,
          titleKeyword: keyword.value,
          property: "update_time",
          flag: false,
          type: selectData.value,
        };
        loading.value = true;
        const data = await fuzzyQueryDataList(jsonData);
        if (data != null && (data as any).code === 0) {
          formatData(data.data.list);
          total.value = data.data.total;
          currentPage.value = 1;
        }
        loading.value = false;
      } else {
        (tree.value as any).setCurrentKey(null);
        if (selectData.value != "") {
          selectData.value = "";
          const jsonData = {
            page: 0,
            size: 8,
            titleKeyword: keyword.value,
            property: "update_time",
            flag: false,
            type: selectData.value,
          };
          loading.value = true;
          const data = await fuzzyQueryDataList(jsonData);
          if (data != null && (data as any).code === 0) {
            formatData(data.data.list);
            total.value = data.data.total;
            currentPage.value = 1;
          }
          loading.value = false;
        }
      }
    };

    const enterHandle = async () => {
      keyword.value = search.value;
      const jsonData = {
        page: 0,
        size: 8,
        titleKeyword: keyword.value,
        property: "update_time",
        flag: false,
        type: selectData.value,
      };
      loading.value = true;
      const data = await fuzzyQueryDataList(jsonData);
      if (data != null && (data as any).code === 0) {
        formatData(data.data.list);
        total.value = data.data.total;
        currentPage.value = 1;
      }
      loading.value = false;
    };

    onMounted(async () => {
      skeletonFlag.value = true;
      const jsonData = {
        page: 0,
        size: 8,
        titleKeyword: keyword.value,
        property: "update_time",
        flag: false,
        type: "",
      };
      const data = await fuzzyQueryDataList(jsonData);
      if (data != null && (data as any).code === 0) {
        formatData(data.data.list);
        total.value = data.data.total;
        skeletonFlag.value = false;
      }
    });

    return {
      defaultProps,
      treeData,
      search,
      dataList,
      Search,
      total,
      getAvatar,
      currentPage,
      pageChange,
      dataListShow,
      ArrowLeft,
      selectDataList,
      cardClick,
      tableData,
      checkboxChange,
      commit,
      skeletonFlag,
      tree,
      nodeClick,
      fileList,
      selectData,
      enterHandle,
      loading,
    };
  },
});
