
import { computed, defineComponent, nextTick, onMounted, PropType, ref } from "vue";
import { ArrowLeft } from "@element-plus/icons-vue";
import { findParameterByType } from "@/api/request";
import { notice } from "@/utils/common";
import { Section, AnalysisDataset, AnalysisParameter, TreeData } from "@/type";
export default defineComponent({
  props: {
    analyseType: {
      type: String,
    },
    analyticDataList: {
      type: Object as PropType<AnalysisDataset[]>,
    },
  },
  emits: ["back", "returnParameter"],
  setup(props, context) {
    const defaultProps = {
      children: "children",
      label: "label",
    };
    const title = computed(() => {
      if (props.analyseType === "section") {
        return "";
      } else if (props.analyseType === "sectionCompare") {
        return "";
      } else if (props.analyseType === "sectionFlush") {
        return "";
      } else if (props.analyseType === "regionFlush") {
        return "";
      } else if (props.analyseType === "elevationFlush") {
        return "";
      } else if (props.analyseType === "flushContour") {
        return "";
      } else if (props.analyseType === "slope") {
        return "";
      } else if (props.analyseType === "volume") {
        return "";
      }
    });
    const placeholder = computed(() => {
      if (
        props.analyseType === "section" ||
        props.analyseType === "sectionCompare" ||
        props.analyseType === "sectionFlush"
      ) {
        return "";
      } else {
        return "";
      }
    });
    const selectFlag = computed(() => {
      if (
        props.analyseType === "elevationFlush" ||
        props.analyseType === "flushContour" ||
        props.analyseType === "slope"
      ) {
        return false;
      } else {
        return true;
      }
    });
    const analyticDataList = computed(() => {
      return props.analyticDataList;
    });
    const treeData = ref<TreeData[]>([]);
    const benchmarkTreeData = ref<TreeData[]>([]);
    const referTreeData = ref<TreeData[]>([]);
    const skeletonFlag = ref(true);

    const sectionValue = ref("");
    const options = ref<Section[]>([]);
    const sectionDem = ref<AnalysisParameter>();
    const sectionDemList = ref<AnalysisParameter[]>([]);
    const benchmarkDem = ref<AnalysisParameter>();
    const referDem = ref<AnalysisParameter>();

    const deep = ref(5);

    const inputValue = ref("");

    const backClick = () => {
      context.emit("back");
    };

    const changeClick = (val: TreeData, type: string) => {
      if (
        props.analyseType === "section" ||
        props.analyseType === "slope" ||
        props.analyseType === "volume"
      ) {
        treeData.value.forEach((item) => {
          item.children.forEach((c) => {
            if (c.id != val.id) {
              c.checkFlag = false;
            } else {
              if (c.checkFlag) {
                sectionDem.value = {
                  fileId: c.id,
                  fileName: c.label,
                  dataListId: item.id,
                  dataListName: item.label,
                  visualId: c.visualId as string,
                  visualType: c.visualType as string,
                };
              } else {
                sectionDem.value = undefined;
              }
            }
          });
        });
      } else if (props.analyseType === "sectionCompare") {
        if (val.checkFlag) {
          sectionDemList.value.push({
            fileId: val.id,
            fileName: val.label,
            dataListId: val.parentId as string,
            dataListName: val.parentName as string,
            visualId: val.visualId as string,
            visualType: val.visualType as string,
          });
        } else {
          for (let i = 0; i < sectionDemList.value.length; i++) {
            if (sectionDemList.value[i].fileId === val.id) {
              sectionDemList.value.splice(i, 1);
              return;
            }
          }
        }
      } else if (
        props.analyseType === "sectionFlush" ||
        props.analyseType === "regionFlush" ||
        props.analyseType === "elevationFlush" ||
        props.analyseType === "flushContour"
      ) {
        if (type === "benchmark") {
          benchmarkTreeData.value.forEach((item) => {
            item.children.forEach((c) => {
              if (c.id != val.id) {
                c.checkFlag = false;
              } else {
                if (c.checkFlag) {
                  benchmarkDem.value = {
                    fileId: c.id,
                    fileName: c.label,
                    dataListId: item.id,
                    dataListName: item.label,
                    visualId: c.visualId as string,
                    visualType: c.visualType as string,
                  };
                } else {
                  benchmarkDem.value = undefined;
                }
              }
            });
          });
        } else if (type === "refer") {
          referTreeData.value.forEach((item) => {
            item.children.forEach((c) => {
              if (c.id != val.id) {
                c.checkFlag = false;
              } else {
                if (c.checkFlag) {
                  referDem.value = {
                    fileId: c.id,
                    fileName: c.label,
                    dataListId: item.id,
                    dataListName: item.label,
                    visualId: c.visualId as string,
                    visualType: c.visualType as string,
                  };
                } else {
                  referDem.value = undefined;
                }
              }
            });
          });
        }
      }
    };

    const btnClick = () => {
      if (props.analyseType === "section") {
        if (
          sectionValue.value != "" &&
          sectionDem.value != undefined &&
          inputValue.value != ""
        ) {
          context.emit("returnParameter", {
            section: sectionValue.value,
            dem: sectionDem.value,
            fileName: inputValue.value,
          });
        } else {
          let target = "";
          if (sectionValue.value === "") {
            target = "";
          } else if (sectionDem.value === undefined) {
            target = "DEM";
          } else {
            target = "";
          }
          notice("warning", "", target + "");
        }
      } else if (props.analyseType === "volume") {
        if (
          sectionValue.value != "" &&
          sectionDem.value != undefined &&
          inputValue.value != ""
        ) {
          context.emit("returnParameter", {
            region: sectionValue.value,
            dem: sectionDem.value,
            deep: deep.value,
            fileName: inputValue.value,
          });
        } else {
          let target = "";
          if (sectionValue.value == "") {
            target = "";
          } else if (sectionDem.value == undefined) {
            target = "DEM";
          } else {
            target = "";
          }
          notice("warning", "", target);
        }
      } else if (props.analyseType === "sectionCompare") {
        if (
          sectionValue.value != "" &&
          sectionDemList.value.length >= 2 &&
          inputValue.value != ""
        ) {
          context.emit("returnParameter", {
            section: sectionValue.value,
            demList: sectionDemList.value,
            fileName: inputValue.value,
          });
        } else {
          let target = "";
          if (sectionValue.value === "") {
            target = "";
          } else if (inputValue.value === "") {
            target = "";
          } else {
            target = "DEM";
          }
          notice("warning", "", target);
        }
      } else if (
        props.analyseType === "sectionFlush" ||
        props.analyseType === "regionFlush"
      ) {
        if (
          sectionValue.value != "" &&
          benchmarkDem.value != undefined &&
          referDem.value != undefined &&
          inputValue.value != ""
        ) {
          if (referDem.value.fileId === benchmarkDem.value.fileId) {
            notice("warning", "", "DEMDEM");
          } else {
            context.emit("returnParameter", {
              section: sectionValue.value,
              benchmarkDem: benchmarkDem.value,
              referDem: referDem.value,
              fileName: inputValue.value,
            });
          }
        } else if (sectionValue.value === "") {
          if (props.analyseType === "sectionFlush") {
            notice("warning", "", "");
          } else if (props.analyseType === "regionFlush") {
            notice("warning", "", "");
          }
        } else if (inputValue.value == "") {
          notice("warning", "", "");
        } else if (benchmarkDem.value === undefined) {
          notice("warning", "", "DEM");
        } else {
          notice("warning", "", "DEM");
        }
      } else if (
        props.analyseType === "elevationFlush" ||
        props.analyseType === "flushContour"
      ) {
        if (
          benchmarkDem.value != undefined &&
          referDem.value != undefined &&
          inputValue.value != ""
        ) {
          if (referDem.value.fileId === benchmarkDem.value.fileId) {
            notice("warning", "", "DEMDEM");
          } else {
            context.emit("returnParameter", {
              benchmarkDem: benchmarkDem.value,
              referDem: referDem.value,
              fileName: inputValue.value,
            });
          }
        } else {
          let target = "";
          if (benchmarkDem.value === undefined) {
            target = "DEM";
          } else if (referDem.value === undefined) {
            target = "DEM";
          } else {
            target = "";
          }
          notice("warning", "", target);
        }
      } else if (props.analyseType === "slope") {
        if (sectionDem.value != undefined && inputValue.value != "") {
          context.emit("returnParameter", {
            dem: sectionDem.value,
            fileName: inputValue.value,
          });
        } else {
          notice(
            "warning",
            "",
            inputValue.value == "" ? "" : "dem"
          );
        }
      }
    };

    const getParame = async (type: string) => {
      const data = await findParameterByType("dem");
      if (data != null && (data as any).code === 0) {
        for (let i = 0; i < data.data.length; i++) {
          let flag = true;
          for (let j = 0; j < treeData.value.length; j++) {
            if (treeData.value[j].id === data.data[i].dataListId) {
              treeData.value[j].children.push({
                id: data.data[i].fileId,
                label: data.data[i].fileName,
                flag: false,
                children: [],
                checkFlag: false,
                visualId: data.data[i].visualId,
                visualType: data.data[i].visualType,
                parentId: data.data[i].dataListId,
                parentName: data.data[i].dataListName,
              });
              flag = false;
              break;
            }
          }
          if (flag) {
            treeData.value.push({
              id: data.data[i].dataListId,
              label: data.data[i].dataListName,
              flag: true,
              children: [],
            });
            treeData.value[treeData.value.length - 1].children.push({
              id: data.data[i].fileId,
              label: data.data[i].fileName,
              flag: false,
              checkFlag: false,
              children: [],
              visualId: data.data[i].visualId,
              visualType: data.data[i].visualType,
              parentId: data.data[i].dataListId,
              parentName: data.data[i].dataListName,
            });
          }
        }
        if (
          type === "sectionFlush" ||
          type === "regionFlush" ||
          type === "elevationFlush" ||
          type === "flushContour"
        ) {
          benchmarkTreeData.value = JSON.parse(JSON.stringify(treeData.value));
          referTreeData.value = JSON.parse(JSON.stringify(treeData.value));
        }
        if (
          type === "section" ||
          type === "sectionCompare" ||
          type === "sectionFlush"
        ) {
          options.value = [];
          analyticDataList.value!.forEach((item) => {
            if (item.visualType === "geoJsonLine") {
              options.value.push({
                id: item.id,
                name: item.fileName,
              });
            }
          });
        } else {
          options.value = [];
          analyticDataList.value!.forEach((item) => {
            if (item.visualType === "geoJsonPolygon") {
              options.value.push({
                id: item.id,
                name: item.fileName,
              });
            }
          });
        }
      }
    };

    onMounted(async () => {
      skeletonFlag.value = true;
      await getParame(props.analyseType as string);
      skeletonFlag.value = false;
    });

    return {
      ArrowLeft,
      title,
      analyticDataList,
      backClick,
      sectionValue,
      options,
      treeData,
      benchmarkTreeData,
      referTreeData,
      defaultProps,
      changeClick,
      btnClick,
      skeletonFlag,
      placeholder,
      selectFlag,
      deep,
      inputValue,
    };
  },
});
