
type Tree = {
  id: string;
  label: string;
  visualType: string;
  visualId: string;
  flag: boolean;
  children: Tree[];
};
import { defineComponent, onMounted, ref } from "vue";
import { Search, Delete } from "@element-plus/icons-vue";
import { updateLayer } from "@/api/request";
import router from "@/router";
import { notice } from "@/utils/common";
import type { DragEvents } from "element-plus/es/components/tree/src/model/useDragNode";

export default defineComponent({
  props: {
    layerList: {
      type: Array,
    },
  },
  emits: ["closeLayer", "hideLayer", "moveLayer"],
  setup(props, context) {
    const defaultProps = {
      children: "children",
      label: "label",
    };
    const serach = ref("");
    const treeData = ref<Tree[]>([]);

    const addLayer = async (val: {
      id: string;
      name: string;
      visualType: string;
      visualId: string;
    }) => {
      for (let i = 0; i < treeData.value.length; i++) {
        if (treeData.value[i].id === val.id) {
          return;
        }
      }
      treeData.value.unshift({
        id: val.id,
        label: val.name,
        visualType: val.visualType,
        children: [],
        flag: true,
        visualId: val.visualId,
      });
      const list: string[] = [];
      treeData.value.forEach((item) => {
        list.push(item.id);
      });
      const data = await updateLayer(
        router.currentRoute.value.params.id as string,
        list
      );
    };

    const closeClick = async (id: string) => {
      for (let i = 0; i < treeData.value.length; i++) {
        if (treeData.value[i].id === id) {
          treeData.value.splice(i, 1);
          const list: string[] = [];
          treeData.value.forEach((item) => {
            list.push(item.id);
          });
          const data = await updateLayer(
            router.currentRoute.value.params.id as string,
            list
          );
          if (data != null && (data as any).code === 0) {
            notice("success", "", "");
          }
          context.emit("closeLayer", id);
          return;
        }
      }
    };

    const delLayer = async (param: string) => {
      for (let i = 0; i < treeData.value.length; i++) {
        if (treeData.value[i].id === param) {
          treeData.value.splice(i, 1);
          const list: string[] = [];
          treeData.value.forEach((item) => {
            list.push(item.id);
          });
          await updateLayer(
            router.currentRoute.value.params.id as string,
            list
          );
          return;
        }
      }
    };

    const changeClick = (val: Tree) => {
      context.emit("hideLayer", { flag: val.flag, id: val.id });
    };

    const allowDrop = (draggingNode: any, dropNode: any, type: string) => {
      if (draggingNode.level === dropNode.level) {
        if (draggingNode.parent.id === dropNode.parent.id) {
          //  || 
          return type === "prev" || type === "next";
        }
      } else {
        // 
        return false;
      }
    };

    const dragHandle = async (
      node: any,
      dropNode: any,
      dropType: any,
      ev: DragEvents
    ) => {
      let target = "";
      console.log(dropType);
      if (dropType === "before") {
        for (let i = 0; i < treeData.value.length; i++) {
          if (treeData.value[i].id === node.data.id) {
            if (i != 0) {
              target = treeData.value[i + 1].id;
            }
            break;
          }
        }
      } else {
        target = dropNode.data.id;
      }
      context.emit("moveLayer", {
        drop: node.data.id,
        target: target,
      });
      const list: string[] = [];
      treeData.value.forEach((item) => {
        list.push(item.id);
      });
      await updateLayer(router.currentRoute.value.params.id as string, list);
    };

    const getLayers = () => {
      return treeData.value.length;
    };

    onMounted(() => {
      props.layerList?.forEach((item: any) => {
        treeData.value.push({
          id: item.id,
          label: item.fileName,
          visualType: item.visualType,
          flag: true,
          children: [],
          visualId: item.visualId,
        });
      });
    });

    return {
      Search,
      serach,
      treeData,
      defaultProps,
      addLayer,
      Delete,
      closeClick,
      changeClick,
      delLayer,
      dragHandle,
      allowDrop,
      getLayers,
    };
  },
});
