
import { defineComponent, ref } from "vue";
import AddDataDialog from "./AddDataDialog.vue";
import AnalyseDialog from "./AnalyseDialog.vue";
import router from "@/router";
export default defineComponent({
  components: {
    AddDataDialog,
    AnalyseDialog,
  },
  emits: ["returnFileList", "operateDraw", "analyse"],
  setup(_, context) {
    const dialogAddData = ref(false);
    const dialogAnalyse = ref(false);
    const state = ref(0);

    const returnData = (
      val: {
        fileId: string;
        fileName: string;
        dataListId: string;
        dataListName: string;
        visualType: string;
        visualId: string;
      }[]
    ) => {
      dialogAddData.value = false;
      context.emit("returnFileList", val);
    };

    const sectionClick = () => {
      if (state.value === 1) {
        state.value = 0;
      } else {
        state.value = 1;
      }
      context.emit("operateDraw", state.value);
    };

    const regionClick = () => {
      if (state.value === 2) {
        state.value = 0;
      } else {
        state.value = 2;
      }
      context.emit("operateDraw", state.value);
    };

    const analyse = (val: { type: string; value: any }) => {
      context.emit("analyse", val);
      dialogAnalyse.value = false;
    };

    const flowClick = () => {
      router.push({ path: "/flow" });
    };

    return {
      state,
      dialogAddData,
      returnData,
      sectionClick,
      regionClick,
      dialogAnalyse,
      analyse,
      flowClick,
    };
  },
});
